import React, { useEffect, useState } from 'react'
import Cart1 from './Cart1';
import Cart3 from './Cart3';
import Cart3_OwlCaroussel from './Cart3-Owlcarousel';

const Cart_section6 = () => {
    const [timeLeft, setTimeLeft] = useState({
        days: 365,
        hours: 0,
        minutes: 0,
        seconds: 0,
    });

    useEffect(() => {
        const interval = setInterval(() => {
            setTimeLeft((prevTime) => {
                const totalSeconds =
                    prevTime.days * 86400 +
                    prevTime.hours * 3600 +
                    prevTime.minutes * 60 +
                    prevTime.seconds;

                if (totalSeconds <= 0) {
                    clearInterval(interval);
                    return { days: 0, hours: 0, minutes: 0, seconds: 0 };
                }

                const newTotalSeconds = totalSeconds - 1;

                const days = Math.floor(newTotalSeconds / 86400);
                const hours = Math.floor((newTotalSeconds % 86400) / 3600);
                const minutes = Math.floor((newTotalSeconds % 3600) / 60);
                const seconds = newTotalSeconds % 60;

                return { days, hours, minutes, seconds };
            });
        }, 1000);
        return () => clearInterval(interval);
    }, []);

    const formatTimeLeft = (time) => {
        return (
            <>
                <span className="countdown-number">{time.days}</span>
                <span className="countdown-string">DAYS</span>{" "}
                <span className="countdown-number">{time.hours}</span>
                <span className="countdown-string">HRS</span>{" "}
                <span className="countdown-number">{time.minutes}</span>
                <span className="countdown-string">MINS</span>{" "}
                <span className="countdown-number">{time.seconds}</span>
                <span className="countdown-string">SECS</span>
            </>
        );
    };

    const customImages = (index) => `/Porto/images/Mobile-assessories${index + 1}.png`;

    return (
        <div className="cart_section_title owl_carousel owl_section_title cart_section6">
            <span className='cart_section_heading_line'>
                <h3>
                    Mobile Accessories
                </h3>
            </span>
            <img src="/Porto/images/MA.jpg" alt="" />
            <p className="cart_2_upper_clock">Hurry up! Offer ends in: <span > {formatTimeLeft(timeLeft)}</span></p>


            <Cart3_OwlCaroussel
                category="mobile-accessories"
                useLocalImages={true}
                customImages={customImages}
            />
        </div>
    );
};


export default Cart_section6